import { BpmnModel } from "./BpmnModel";
import { BpmnModelHelper } from "./BpmnModelHelper";
import { Gateway } from "./Gateway";
import { GatewayType } from "./GatewayType";
import { IHasDefaultSequenceFlow } from "./IHasDefaultSequenceFlow";

export class InclusiveGateway extends Gateway implements IHasDefaultSequenceFlow {
    constructor(model: BpmnModel) {
        super(model);
    }

    readonly ClrTypeID: string = "bpmn.InclusiveGateway";
    IncomingGatewayType: string = GatewayType.Inclusive;
    OutgoingGatewayType: string = GatewayType.Inclusive;

    NewInstance(): InclusiveGateway {
        return new InclusiveGateway(this.Model);
    }

    Default: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.Default = null;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "Default", this.Default);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.Default = obj.Default;
    }
}